<template>
  <div class="return_time_detail">
    <div class="left_menu">
      <el-select
        v-model="formData.channel"
        placeholder="请选择渠道"
        @change="changeChannel"
      >
        <el-option
          v-for="(item, i) in channels"
          :key="i"
          :label="item"
          :value="item"
        >
        </el-option>
      </el-select>
      <div class="subMenu">
        <el-menu
          :unique-opened="true"
          default-active="1-1"
          class="el-menu-vertical-demo"
          @select="selectStation"
        >
          <el-submenu
            v-for="(item, i) in storeMenu"
            :key="i"
            :index="i + 1 + ''"
          >
            <template slot="title">
              <span>{{ item.country }}</span>
            </template>
            <el-menu-item-group>
              <el-menu-item
                v-for="(item, j) in item.stations"
                :key="j"
                :index="i + 1 + '-' + (j + 1)"
                >{{ item }}</el-menu-item
              >
            </el-menu-item-group>
          </el-submenu>
        </el-menu>
      </div>
    </div>
    <div class="rightbox">
      <div class="topOption">
        <el-form :inline="true" :model="formOption" class="demo-form-inline">
          <el-form-item label="下单时间">
            <el-date-picker
              v-model="formOption.purchase_date"
              type="date"
              value-format="yyyy-MM-dd"
              placeholder="选择日期"
            >
            </el-date-picker>
          </el-form-item>
          <el-form-item label="sku">
            <el-select v-model="formOption.sku" placeholder="请选择sku">
              <el-option
                v-for="(item, i) in skus"
                :key="i"
                :label="item"
                :value="item"
              >
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="仓库名称">
            <el-select
              v-model="formOption.warehouse_name"
              placeholder="请选择仓库名称"
            >
              <el-option
                v-for="(item, i) in warehouses"
                :key="i"
                :label="item"
                :value="item"
              >
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="订单号">
            <el-input
              v-model="formOption.order_id"
              placeholder="请选择订单号"
            ></el-input>
          </el-form-item>
          <el-form-item label="退货方式">
            <el-select
              v-model="formOption.refund_type"
              placeholder="请选择退货方式"
            >
              <el-option
                v-for="(item, i) in refundWays"
                :key="i"
                :label="item"
                :value="item"
              >
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="search">搜索</el-button>
          </el-form-item>
        </el-form>
      </div>
      <div class="detail_table">
        <el-table border :data="tableData">
          <el-table-column type="index" label="序号" width="111">
          </el-table-column>
          <el-table-column prop="channel" label="渠道" width="111">
          </el-table-column>
          <el-table-column prop="station" label="站点" width="111">
          </el-table-column>
          <el-table-column prop="country" label="国家" width="111">
          </el-table-column>
          <el-table-column prop="type" label="类型" width="111">
          </el-table-column>
          <el-table-column prop="sku" label="sku" width="111">
          </el-table-column>
          <el-table-column prop="product_name" label="品名" width="111">
          </el-table-column>
          <el-table-column prop="warehouse_type" label="仓库类型" width="111">
          </el-table-column>
          <el-table-column prop="warehouse_code" label="仓库编码" width="111">
          </el-table-column>
          <el-table-column prop="purchase_date" label="下单时间" width="111">
          </el-table-column>
          <el-table-column prop="refund_method" label="退货方式" width="111">
          </el-table-column>
          <el-table-column prop="tracking_no" label="退货追踪编码" width="111">
          </el-table-column>
          <el-table-column
            prop="inter_warehouse_date"
            label="送达仓库时间"
            width="111"
          >
          </el-table-column>
        </el-table>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      tableData: [],
      formData: {
        channel: "",
        station: "",
        country: ""
      },
      formOption: {
        purchase_date: "",
        sku: "",
        warehouse_name: "",
        order_id: "",
        refund_type: ""
      },
      channels: [],
      stations: [],
      countries: [],
      skus: [],
      warehouses: [],
      refundWays: [],
      storeMenu: [],
      data2: [],
      data3: []
    };
  },
  methods: {
    search() {
      // 搜索
      let formData1 = new FormData();
      for (let key in this.formData) {
        if (!this.formData[key] || !this.formData[key].length) {
          continue;
        }
        console.log(this.formData[key], "键");
        formData1.append(key, this.formData[key]);
      }
      for (let key in this.formOption) {
        if (!this.formOption[key] || !this.formOption[key].length) {
          continue;
        }
        console.log(this.formOption[key], "键");
        formData1.append(key, this.formOption[key]);
      }
      console.log("搜索", formData1);
      let url = "server/order_management/refund_times_deatil_data/";
      this.$axios.post(this._url2 + url, formData1).then(res => {
        this.tableData = res.data.data;
      });
    },
    selectStation(index) {
      let [firstIdx, lastIdx] = index.split("-").map(i => {
        return Number(i) - 1;
      });

      this.formData.country = this.storeMenu[firstIdx].country;
      this.formData.station = this.storeMenu[firstIdx].stations[lastIdx];
      this.getInitData();
    },
    changeChannel() {
      this.storeMenu = [];
      if (this.formData.channel !== "all") {
        let idx = this.channels.findIndex(i => i === this.formData.channel);
        let countryData = this.data2[idx];
        countryData.forEach((item, index) => {
          let obj = {};
          obj["country"] = item;
          obj["stations"] = this.data3[idx][index];
          this.storeMenu.push(obj);
        });
      }
    },
    getInitData() {
      let formData = new FormData();
      for (let key in this.formData) {
        formData.append(key, this.formData[key]);
      }
      let url = "server/order_management/refund_times_deatil_data/";
      this.$axios.post(this._url2 + url, formData).then(res => {
        console.log("发起请求");
        this.tableData = res.data.data;
      });
    },
    getStations(country) {
      this.storeMenu[idx] = {};
      this.storeMenu[idx].title = country;
      let url = "server/personnel_management/get_sign_area/";
      this.$axios
        .get(this._url2 + url, {
          params: {
            country
          }
        })
        .then(res => {
          this.stations = res.data.data;
          this.storeMenu[idx].country = j.country;
        });
    },
    getSearchList() {
      this.channels = [];
      let url = "server/personnel_management/area_sign1/";
      this.$axios.get(this._url2 + url).then(res => {
        this.channels = res.data.data1;
        // 国家 data2
        // 站点 data3
        this.data2 = res.data.data2;
        this.data3 = res.data.data3;
        this.formData.channel = this.channels[0];
        this.formData.country = this.data2[0][0];
        console.log(this.formData.country);
        this.stations = this.data3[0][0];
        console.log(this.stations);
        this.formData.station = this.stations[0];
        this.changeChannel();
        this.getInitData();
      });
    }
  },
  created() {
    this.getSearchList();
  }
};
</script>

<style scoped>
.return_time_detail {
  display: flex;
  flex-wrap: nowrap;
  padding-top: 40px;
}
.return_time_detail .left_menu {
  width: 250px;
  padding-left: 50px;
}
.return_time_detail .el-select {
  margin-bottom: 30px;
}
.return_time_detail .subMenu {
  background: rgba(255, 255, 255, 0.9);
}
.return_time_detail .rightbox {
  border: 1px solid #fff;
  padding: 30px;
  margin: 0 auto;
}
.return_time_detail >>> .el-form-item__label {
  color: #fff;
}
.return_time_detail .el-table {
  width: 1445px;
  margin: 0 auto;
}
</style>



